@import url("https://fonts.googleapis.com/css?family=Amatic+SC&display=swap");

.phoneClassActive {
  font-weight: bold;
  border-bottom: 2px solid #000;
}

.App {
  margin: 0;
  padding: 0;
}

.nav__main_container {
  border: 1px solid black;
  height: 30px;
  background: rgb(53, 83, 83);
  display: flex;
  justify-content: space-between;
  padding: 20px 45px;
}

.Phones__page {
  text-decoration: none;
  color: #e0e0e0;
}

.page__home {
  text-decoration: none;
}

.wrapper__loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation: load7 1.8s infinite ease-in-out;
  animation-fill-mode: both;
}

.loader {
  color: #0e0d0d;
  font-size: 10px;
  margin: 80px auto;
  position: relative;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
}

.loader:before,
.loader:after {
  content: " ";
  position: absolute;
  top: 0;
}

.loader:before {
  left: -3.5em;
  animation-delay: -0.32s;
}

.loader:after {
  left: 3.5em;
}

@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
