.card {
  height: 350px;
  width: 360px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 30px 15px 15px;
  box-sizing: border-box;
  font-size: 11px;
  line-height: 13px;
}

.card__img {
  width: 160px;
  height: 134px;
  display: block;
}

.card__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin-bottom: 5px;
  padding: 0;
}

.card__block-price {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 20px;
}

.card__price-number {
  font-size: 10px;
  line-height: 10px;
}

.card__button {
  font-weight: bold;
  font-size: 14px;
  line-height: 40px;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid #00acdc;
  border-radius: 5px;
  display: block;
  cursor: pointer;
  text-decoration: none;
  width: 170px;
  height: 40px;
  box-sizing: border-box;
  background: #00acdc;
}

.card__button:hover {
  background: #fff;
  color: #00acdc;
}

.catalog__phones {
  display: grid;
  justify-content: center;
  margin: 50px 0;
}
