html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  perspective: 1px;
  transform-style: preserve-3d;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: "Amatic SC", cursive;
  color: #fff;
}

.home_page {
  box-sizing: border-box;
  min-height: 100vh;
  text-align: center;
  padding-top: 20%;
  transform-style: inherit;
  height: 100vw;
}

.home_page::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: -1;
  transform-origin: center;
  min-height: 100vh;
  background-image: url("https://images.unsplash.com/photo-1556742517-fde6c2abbe11?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=334&q=80");
  background-repeat: no-repeat;
  background-size: cover;

  /* Паралакс ефект */
  transform: translateZ(-1px) scale(2);
}

.home_page h1 {
  margin-top: -100px;
  font-size: 42px;
}

.navlink {
  color: #e0e0e0;
}

